import React from 'react';
import PopupWithForm   from './PopupWithForm';
import {CurrentUserContext} from "./../contexts/CurrentUserContext";

function EditProfilePopup({isOpen,onClose,onUpdateUser}) {
    const currentUser = React.useContext(CurrentUserContext);
    const [name, setName]=React.useState('');
    const [description¬†, setDescription]=React.useState("");
   
    const changeName=(e)=>{
        setName(e.target.value);  
    }

    const changeDescription=(e)=>{
        setDescription(e.target.value);  
    }

    // –ü–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏ —Ç–µ–∫—É—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ API
    // –µ–≥–æ –¥–∞–Ω–Ω—ã–µ –±—É–¥—É—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω—ã –≤ —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞—Ö.
    React.useEffect(() => {
      setName(currentUser.name);
      setDescription(currentUser.about);
    }, [currentUser]);

    function handleSubmit(e) {
      // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
      e.preventDefault();    
      // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
      onUpdateUser({
        name,
        about: description,
      });
    }

  return ( 
    <PopupWithForm name="edit" title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å" isOpen={isOpen} onClose={onClose} buttonText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å" onSubmit¬†={handleSubmit}>
    <div className="form__container">
    <input type="text" value={name} onChange ={changeName} className="popup__input popup__input_type_name" id="name" name="nameInput" placeholder="–ò–º—è" required  />
    <span className="popup__input-error name-error"></span>
    <input type="text" value={description} onChange ={changeDescription} className="popup__input popup__input_type_job" id="job" name="jobInput" placeholder="–û —Å–µ–±–µ" required  />
    <span className="popup__input-error job-error"></span>
    </div>
    </PopupWithForm>
   
  );
}

export default EditProfilePopup;