import React from 'react';
import PopupWithForm   from './PopupWithForm';

function AddPlacePopup¬†({isOpen,onClose,onAddPlace}) {

    const [name, setName]=React.useState('');
    const [link, setLink]=React.useState('');
   
    const changeName=(e)=>{
        setName(e.target.value);  
    }

    const changeLink=(e)=>{
      setLink(e.target.value);  
    }

    function handleSubmit(e) {
      // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
      e.preventDefault();    
      // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
      onAddPlace({
        name,
        link,
      });
      setName('');
      setLink(''); 
    }

  return ( 
    <PopupWithForm name="add" title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ" isOpen={isOpen} onClose={onClose} buttonText="–°–æ–∑–¥–∞—Ç—å" onSubmit={handleSubmit}>
        <div className="form__container">
        <input value={name} onChange ={changeName} type="text" className="popup__input popup__input_type_text" id="add" name="name" placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ" required />
        <span className="popup__input-error add-error"></span>
        <input value={link} onChange ={changeLink}type="url" className="popup__input popup__input_type_link" id="url" name="link" placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É" required />
        <span className="popup__input-error url-error"></span>
        </div>
    </PopupWithForm> 
  );
}

export default AddPlacePopup;