import React from 'react';
import PopupWithForm   from './PopupWithForm';

function EditAvatarPopup({isOpen,onClose,onUpdateAvatar}) {
  const refInput = React.useRef(''); // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –æ–±—ä–µ–∫—Ç, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π —Ö—É–∫–æ–º, –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é

    function handleSubmit(e) {
      e.preventDefault();
      onUpdateAvatar({
        avatar: refInput.current.value,
      });
      refInput.current.value='';
    }

    

  return ( 
    <PopupWithForm name="avatar" title="–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä" isOpen={isOpen} onClose={onClose} buttonText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å" onSubmit¬†={handleSubmit}>
        <div className="form__container">
        <input type="url" ref={refInput} className="popup__input popup__input_type_text" id="avatar" name="avatar" placeholder="https://somewebsite.com/someimage.jpg" required />
        <span className="popup__input-error avatar-error"></span>
        </div>
    </PopupWithForm>  
  );
}

export default EditAvatarPopup;
