import React from 'react';
import {CurrentUserContext} from "./../contexts/CurrentUserContext";

function Card(props) {
  const currentUser = React.useContext(CurrentUserContext);

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const isOwn = props.card.owner._id === currentUser._id;

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  const cardDeleteButtonClassName = (
    `card__vector-delete ${isOwn ? 'card__vector-delete_type_activ' : ''}`
  );

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
  const isLiked = props.card.likes.some(i => i._id === currentUser._id);

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
  const cardLikeButtonClassName = `card__vector-like ${isLiked ? 'card__vector_active' : ''}`;

    function handleClick() {
        props.onCardClick(props.card);
      } 

      function handleLikeClick¬†() {
        props.onCardLike(props.card);
      } 
      
      function handleDeleteClick() {
        props.onCardDelete(props.card);
      }
    //  console.log(props);

  return (  
    <article className="card" id={props.card._id}>
     <img className="card__image" src={props.card.link} alt={props.card.name} onClick={handleClick}/>
     <div className="card__text">
        <h2 className="card__title">{props.card.name}</h2>
        <div className="card__like-group">
           <button type="button" className={cardLikeButtonClassName} onClick={handleLikeClick}></button>
           <label className="card__amount-like">{props.card.likes.length}</label>
        </div>
     </div>
     <button type="button" className={cardDeleteButtonClassName} onClick={handleDeleteClick}/>
     </article>
   
  );
}

export default Card;